<template>
	<div class="recommend-container">
		<div class="recommend-header">
			<h2 class="title"></h2>
			<div class="control-part">
				<span
					@click="slidePre"
					class="control control-left"
					:class="{'disable': this.currPage2 === 0}">
					<i class="iconfont">&#xe621;</i>
				</span>
				<span
					@click="slideNext"
					class="control control-right"
					:class="{'disable': this.currPage2 === Math.floor((this.recomends.length - 1) / 5)}">
					<i class="iconfont">&#xe606;</i>
				</span>
			</div>
		</div>
		<div class="recommend-wrap">
			<ul class="recommend-content clearfix">
				<li class="recomend-item"
					v-for="item in recomends">
					<a :href="item.link">
						<img class="item-image" :src="item.imgUrl" :alt="item.name">
					</a>
					<h3 class="title">
						<a :href="item.link">{{item.name}}</a>
					</h3>
					<p class="price">{{item.price}}</p>
					<p class="favorable" v-show="item.favorStatus">
						{{item.favorable}}
					</p>
					</a>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>

import $ from 'jquery'

export default {
	name: 'Recommend',
	data () {
		return {
			currPage2: 0,
			recomends: [
				{link: 'http://item.mi.com/1163900098.html', imgUrl: './static/imgs/xmds65.jpg', name: '3s 65 ', price: '6499', favorable: '',favorStatus: false},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/ydsb.jpg', name: 'Amazfit ', price: '799', favorable: '   5718    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/mjxcjly.jpg', name: '', price: '329', favorable: '   6373    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/mjddhb.jpg', name: '', price: '1999', favorable: '   2164    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xmydlyej.jpg', name: '', price: '139', favorable: '   1    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xmyd2.jpg', name: '10000mAh2', price: '79', favorable: '   4.6    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xxetsb.jpg', name: ' ', price: '199', favorable: '   2882    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xmlyq.jpg', name: ' HD', price: '1299', favorable: '    613    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xmvr2.jpg', name: 'VRPLAY2', price: '99', favorable: '      593    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/mjydxnk.jpg', name: '() ', price: '229', favorable: '   1601    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/yddy.jpg', name: '10000mAh', price: '129', favorable: '   8254    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xmwlyx.jpg', name: '', price: '349', favorable: '      6192    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xmhsej.jpg', name: ' ', price: '29', favorable: '   4.6    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xmlyejqc.jpg', name: '', price: '59', favorable: '   2.8    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xmjtyy.jpg', name: '', price: '1999', favorable: '   5718    ',favorStatus: false},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/90flxx.jpg', name: '90 20', price: '269', favorable: '   2.3    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/90f24lxx.jpg', name: '90 20', price: '269', favorable: '   2.3    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xmcxb.jpg', name: '3USB 2A', price: '49', favorable: '   28.5    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/xmxgp.jpg', name: '2', price: '129', favorable: '   2.7    ',favorStatus: true},
				{link: 'http://item.mi.com/1164300010.html', imgUrl: './static/imgs/mtjmjqr.jpg', name: '', price: '459', favorable: '   4321    ',favorStatus: true}
			]
		}
	},
	methods: {
		slideNext () {
			if (this.currPage2 < Math.floor((this.recomends.length - 1) / 5)) {
				this.currPage2++
				$('.recommend-content').css('margin-left', this.currPage2 * (-1224) + 'px')
			}
		},
		slidePre () {
			if (this.currPage2 !== 0) {
				this.currPage2--
				$('.recommend-content').css('margin-left', this.currPage2 * (-1224) + 'px')
			}
		}
	}
}
</script>

<style scoped lang="scss">
	.iconfont {
	  font-family:"iconfont" !important;
	  color: #b0b0b0;
    font-size: 10px;
	  font-style:normal;
	  -webkit-font-smoothing: antialiased;
	  -moz-osx-font-smoothing: grayscale;
	}
	.recommend-container {
		width: 1226px;
		height: auto;
		margin: 0 auto;
		padding-bottom: 40px;
		>.recommend-header {
			position: relative;
			.title {
				margin: 0;
				font-size: 22px;
				font-weight: 200;
				line-height: 58px;
				color: #333;
			}
			.control-part {
				position: absolute;
				top: 10px;
				right: 0;
				float: left;
				display: block;
				.control {
					display: block;
					float: left;
					width: 34px;
					height: 23px;
					line-height: 23px;
					text-align: center;
					border: 1px solid #e0e0e0;
					cursor: pointer;
					&:hover {
						.iconfont {
							color: #ff6700;
						}
					}
					&.disable {
						&:hover {
							.iconfont {
								color: #e0e0e0;
							}
						}
						.iconfont {
							color: #e0e0e0;
						}
					}
					.iconfont {
						color: #b0b0b0;
						font-size: 10px;
					}
				}
			}
		}
		>.recommend-wrap {
			width: 1226px;
			height: 340px;
			overflow: hidden;
			>.recommend-content {
				width: auto;
				height: 340px;
				transition: all 0.3s;
				.recomend-item {
					float: left;
					display: block;
					height: 300px;
					width: 234px;
					margin-right: 14px;
			    padding-top: 39px;
			    border-top-width: 1px;
			    border-top-style: solid;
			    text-align: center;
			    background: #fff;
			    -webkit-transition: all .6s;
			    transition: all .6s;
					>a {
						display: block;
				    width: 140px;
				    margin: 0 auto 22px;
				    >.item-image {
							width: 140px;
				    	height: 140px;
						}
					}
					>.title {
						margin-bottom: 10px;
				    font-size: 14px;
				    font-weight: 400;
				    text-overflow: ellipsis;
				    white-space: nowrap;
				    overflow: hidden;
				    >a {
				    	color: #212121;
				    }
					}
					>.favorable {
						height: 18px;
				    color: #757575;
					}
					>.price {
						margin-bottom: 10px;
			    	color: #ff6709;
					}
					&:nth-child(5n+1) {
						border-top-color: #ffac13;
					}
					&:nth-child(5n+2) {
						border-top-color: #83c44e;
					}
					&:nth-child(5n+3) {
						border-top-color: #2196f3;
					}
					&:nth-child(5n+4) {
						border-top-color: #e53935;
					}
					&:nth-child(5n) {
						margin-right: -3px;
						border-top-color: #00c0a5;
					}	
				}
			}
		}
	}
</style>



